<script setup>
import { ref, watch, onMounted, onBeforeUnmount, nextTick } from "vue";
import axios from 'axios';
import Charts from "@/components/Charts.vue";

import { useUserStore } from "@/stores/user.js";
const userData = useUserStore();

const settings1 = ref({});
settings1.value.title = "Daily Trend";
settings1.value.type = "line";
settings1.value.api = {
    url: userData.apiBaseUrl + "/call/de_running_total",
    params: [14],
}
settings1.value.columns = {
    label: "date",
    datas: ["running_total", "daily_down", "daily_done"],
};
settings1.value.colors = ["red", "orange", "green"];
settings1.value.labels = ["Total Down", "Units Down / Day", "Units Done / Day"];


const settings2 = ref({});
settings2.value.title = "Unit Summary";
settings2.value.type = "pie";
settings2.value.api = {
    url: userData.apiBaseUrl + "/call/de_total_rental",
}
settings2.value.columns = {
    datas: ["unit_rental", "spare_exists_false", "spare_exists_true"],
}
settings2.value.colors = ["green", "red", "orange"];
settings2.value.labels = ["Unit Rental", "PA Unit", "PA Rental"];


const settings3 = ref({});
settings3.value.title = "Customer Summary";
settings3.value.type = "bar";
settings3.value.api = {
    url: userData.apiBaseUrl + "/call/de_customer_summary",
}
settings3.value.columns = {
    label: ["customer"],
    datas: ["total_unit", "total_break"],
}
settings3.value.colors = ["green", "red"];
settings3.value.labels = ["Total Unit", "Total Break"];

const settings4 = ref({});
settings4.value.title = "Location Summary";
settings4.value.type = "bar";
settings4.value.api = {
    url: userData.apiBaseUrl + "/call/de_location_summary",
}
settings4.value.columns = {
    label: ["alt_location"],
    datas: ["total_unit", "total_break"],
}
settings4.value.colors = ["green", "red"];
settings4.value.labels = ["Total Unit", "Total Break"];

const isActive = ref([true, false, false]);
const allActive = ref(false);
const setActive = (index) => {
    if(isActive.value[index] && !allActive.value){
        for (let i = 0; i < isActive.value.length; i++) {
            isActive.value[i] = true;
        }
        allActive.value = true;
    } else{
        for (let i = 0; i < isActive.value.length; i++) {
            isActive.value[i] = (i === index);
        }
        allActive.value = false;
    }
};

</script>

<template>
    <div>
        <button @click="setActive(0)">0</button>
        <button @click="setActive(1)">1</button>
        <button @click="setActive(2)">2</button>
        <Charts v-if="isActive[0]" :settings=settings1></Charts>
        <Charts v-if="isActive[1]" :settings=settings2></Charts>
        <Charts v-if="isActive[2]" :settings=settings3></Charts>
        <Charts v-if="isActive[2]" :settings=settings4></Charts>
    </div>
</template>

<style scoped>

</style>